/* Apply padding to respect the safe area */
body {
    background-color: black; /* Set the background color to black */
}

.BGImageContainerH {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    background-attachment: fixed;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100vw;
    box-sizing: border-box;
}


.HeaderContainer {
    display: flex;
    align-items: center; 
    justify-content: space-between; 
    width: 100%;  
    margin-top: 2%;
    /*gap: min(60px, 90%);*/
    box-sizing: border-box;
    align-items: center;
}

.NameContainer {
    color: rgb(170, 170, 170);  
    font-size: 36px;  
    padding-left: max(8%,0%);  
    display: flex;
    align-items: center; 
    width: auto; 
    white-space: nowrap; 
    box-sizing: border-box;
}

.menu {
    background-color: transparent;  
    display: flex;  
    justify-content: flex-end;  
    margin-right: 2%;
    flex-shrink: 0;
    box-sizing: border-box;
}

.menu ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: flex-end;
    margin-right: 3%;
    background-color: transparent;
}

.menu li {
    margin-left: 6%;
    background-color: transparent;
    transition: background-color 0.3s ease, color 0.3s ease;
    flex-shrink: 0;
}
.menu a {
    color: rgb(170, 170, 170);
    text-decoration: none;
}

.menu a:hover {
    color: rgb(137, 77, 0);  
    
}


.FirstContainerH {
    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.7));
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%; 
    height: calc(100vh - 4%);  
    color: white;
}



.LicContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 92%;
    background-color: rgb(9, 9, 9);
}


h1 {
    font-size: 48px;
    margin-bottom: 10px;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5); 
    color: white;
    font-family: 'Rajdhani', sans-serif;
    text-align: center;

}

p {
    font-size: 24px;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5); 
    color: white;
    font-weight: 200;
    
}

h2 {
    font-size: 12px;
    color: rgb(65, 65, 65);
    text-align: center;

}



 /* QualityControl */



 .BGImageContainer {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    height: max(100vh, 100%);
    width: 100%; 
    
}
 
 .FirstContainer {
    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 1) 95%);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%; 
    min-height: 100vh; /*Why ?????????*/
    /*height: calc(max(100vh, 100%) - max(4vh, 4%));*/ 
    color: white;
}

.CntFirstContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 3%;  
}

.QualityControlPanelContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 2%; /* 20px */
    padding: 13% 20%; /* 60px */
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.5), 0 6px 20px 0 rgba(0, 0, 0, 0.1);
    background: rgba(30, 30, 30, 0.6); 
    box-sizing: border-box;
    margin: auto; /* Centers the container if less than 100% width */
}

.Info_PhotoContainer {
    border: 2px dashed #747474; /*Discontinuous borders */
    /*padding: 10px 80px; */
    padding: 2% 10%;
    box-sizing: border-box;
}

.PredictionContainer,
.ImageContainer {
    min-height: 150px;
}

.placeholder {
    color: #ccc;
    text-align: center;
    flex-shrink: 0;
    box-sizing: border-box;
    
}

.ButtonsContainer {
    margin-top: 20%;
    display: flex;
    flex-direction: row; 
    justify-content: center;
    gap: 3%; 
}


.image-container img {
    max-width: 100%;
    max-height: 100%; 
}




.black-button {
    background-color: #000000;
    color: #FFFFFF;
    border: none;
    padding: 5% 10%;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 3% 3%;
    cursor: pointer;
    border-radius: 8px;
    flex-shrink: 0;
    box-sizing: border-box;
}
  
.gold-button {
    background-color: rgb(180, 102, 0);
    color: #000000;
    border: none;
    padding: 2% 4%;    /* 1st for top-bottom and 2nd for left-right*/
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 3% 3%;
    cursor: pointer;
    border-radius: 8px;
    flex-shrink: 0;
    box-sizing: border-box;

/* Optionnal: add a gradient
background-image: linear-gradient(to bottom, #ffd700 0%, #ffae00 100%);
*/
}

