}

h6 {
    font-size: 24px;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5); 
    color: white;
    font-weight: 500;
    margin-top: 15px; 
    margin-bottom: 50px;
}



/*   Gold Prediction   */


.InputsContainer {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 10%; /*40px*/
    box-sizing: border-box;
}


.GoldClosingPriceFormEl {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 3vh; /*20px*/
    box-sizing: border-box;
}

.gold-buttonGldpr {
    background-color: rgb(180, 102, 0);
    color: #000000;
    border: none;
    padding: 2% 4%;    /* 1st for top-bottom and 2nd for left-right*/
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 3% 3%;
    cursor: pointer;
    border-radius: 8px;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
    width: 100%; /* ou la largeur que vous souhaitez */   /*100px*/
    border-radius: 10px; /* cela crée des bords arrondis */
    border: 1px solid #ccc; /* un exemple de bordure, modifiez comme vous le souhaitez */
    padding: 5px; /* Ajout de padding interne pour que le texte ne touche pas les bords */
    box-sizing: border-box;
  }



.GoldPredictionResultContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 3%; 
    box-sizing: border-box;
}

.KaggleLink {
    color: rgb(180, 102, 0);
    text-decoration: underline;
  }



  /* Media Queries pour la réactivité sur différentes tailles d'écran */
@media only screen and (max-width: 480px) {
    /* Styles pour les téléphones mobiles */
    .NameContainer {
        color: rgb(170, 170, 170);  
        font-size: 22px;  
        padding-left: max(5%,0%);  
        margin-top: 2vw;
    }

    .menu li {
        font-size: 12px;
    }

    
    p {
        font-size: 18px;
        text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5); 
        color: white;
        font-weight: 200;
        text-align: center;
        
    }

    h1 {
        font-size: 30px;
        margin-bottom: 10px;
        text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5); 
        color: white;
        font-family: 'Rajdhani', sans-serif;
        text-align: center;
    
    }
    
    h2 {
        font-size: 8px;
        color: rgb(65, 65, 65);
        text-align: center;
    
    }

    
}

